"use client"

/**
 * ============================================================================
 * VUA TI·∫æNG VI·ªÜT - IOTA SMART CONTRACT HOOK
 * ============================================================================
 * 
 * Hook t∆∞∆°ng t√°c v·ªõi smart contract game ƒë·ªë ch·ªØ "Vua Ti·∫øng Vi·ªát"
 * 
 * T√≠nh nƒÉng:
 * - T·∫°o c√¢u h·ªèi v·ªõi ƒë√°p √°n ƒë√£ hash (ch·ªëng gian l·∫≠n)
 * - Tr·∫£ l·ªùi c√¢u h·ªèi v√† nh·∫≠n th∆∞·ªüng t·ª± ƒë·ªông
 * - Xem th·ªëng k√™ game
 * 
 * ============================================================================
 */

import { useState, useCallback, useEffect } from "react"
import {
    useCurrentAccount,
    useIotaClient,
    useSignAndExecuteTransaction,
} from "@iota/dapp-kit"
import { Transaction } from "@iota/iota-sdk/transactions"
import { useNetworkVariable } from "@/lib/config"
import { GAME_STATE_ID } from "@/lib/config"
import { keccak256 } from "js-sha3"

// ============================================================================
// CONTRACT CONFIGURATION
// ============================================================================

export const CONTRACT_MODULE = "contract"
export const CONTRACT_METHODS = {
    CREATE_QUESTION: "create_question",
    SUBMIT_ANSWER: "submit_answer",
    CANCEL_QUESTION: "cancel_question",
    ADD_REWARD: "add_reward",
    HASH_ANSWER: "hash_answer",
} as const

// ============================================================================
// TYPES
// ============================================================================

export interface Question {
    id: string
    questionText: string
    hint: string
    rewardAmount: number
    isActive: boolean
    creator: string
    createdAt: number
    deadline: number
    winner: string | null
}

export interface GameStats {
    totalQuestions: number
    totalSolved: number
    totalRewardsDistributed: number
}

export interface TransactionState {
    isLoading: boolean
    isPending: boolean
    hash: string | undefined
    error: Error | null
    isConfirmed: boolean
}

// ============================================================================
// HELPER FUNCTIONS
// ============================================================================

/**
 * Hash ƒë√°p √°n v·ªõi salt s·ª≠ d·ª•ng keccak256
 * Ph·∫£i kh·ªõp v·ªõi c√°ch hash trong smart contract
 */
export function hashAnswer(answer: string, salt: string): string {
    const data = answer + salt
    const hash = keccak256(data)
    return hash
}

/**
 * Chuy·ªÉn string th√†nh vector<u8> (bytes)
 */
export function stringToBytes(str: string): number[] {
    return Array.from(new TextEncoder().encode(str))
}

/**
 * Chuy·ªÉn hex string th√†nh bytes array
 */
export function hexToBytes(hex: string): number[] {
    const bytes: number[] = []
    for (let i = 0; i < hex.length; i += 2) {
        bytes.push(parseInt(hex.substr(i, 2), 16))
    }
    return bytes
}

/**
 * Chuy·ªÉn bytes th√†nh string
 */
export function bytesToString(bytes: number[] | Uint8Array): string {
    return new TextDecoder().decode(new Uint8Array(bytes))
}

// ============================================================================
// MAIN HOOK
// ============================================================================

export const useVuaTiengViet = () => {
    const currentAccount = useCurrentAccount()
    const address = currentAccount?.address
    const packageId = useNetworkVariable("packageId")
    const iotaClient = useIotaClient()
    const { mutate: signAndExecute, isPending } = useSignAndExecuteTransaction()

    // State
    const [gameStateId, setGameStateId] = useState<string | null>(null)
    const [questions, setQuestions] = useState<Question[]>([])
    const [gameStats, setGameStats] = useState<GameStats | null>(null)
    const [isLoading, setIsLoading] = useState(false)
    const [hash, setHash] = useState<string | undefined>()
    const [error, setError] = useState<Error | null>(null)
    const [isConfirmed, setIsConfirmed] = useState(false)

    // Load game state ID from config or localStorage
    useEffect(() => {
        if (typeof window !== "undefined") {
            // ∆Øu ti√™n d√πng GAME_STATE_ID t·ª´ config
            if (GAME_STATE_ID) {
                setGameStateId(GAME_STATE_ID)
            } else {
                const savedGameStateId = localStorage.getItem("vuaTiengViet_gameStateId")
                if (savedGameStateId) {
                    setGameStateId(savedGameStateId)
                }
            }
        }
    }, [])

    // Save game state ID to localStorage
    const saveGameStateId = (id: string) => {
        setGameStateId(id)
        if (typeof window !== "undefined") {
            localStorage.setItem("vuaTiengViet_gameStateId", id)
        }
    }

    /**
     * Fetch GameState object ƒë·ªÉ l·∫•y th·ªëng k√™
     */
    const fetchGameStats = useCallback(async () => {
        if (!gameStateId || !iotaClient) return

        try {
            const response = await iotaClient.getObject({
                id: gameStateId,
                options: { showContent: true },
            })

            if (response.data?.content?.dataType === "moveObject") {
                const fields = response.data.content.fields as any
                setGameStats({
                    totalQuestions: parseInt(fields.total_questions || "0"),
                    totalSolved: parseInt(fields.total_solved || "0"),
                    totalRewardsDistributed: parseInt(fields.total_rewards_distributed || "0"),
                })
            }
        } catch (err) {
            console.error("Error fetching game stats:", err)
        }
    }, [gameStateId, iotaClient])

    /**
     * Fetch danh s√°ch c√¢u h·ªèi
     */
    const fetchQuestions = useCallback(async () => {
        if (!packageId || !iotaClient) return

        try {
            setIsLoading(true)

            // Query t·∫•t c·∫£ Question objects thu·ªôc package n√†y
            const response = await iotaClient.queryEvents({
                query: {
                    MoveEventType: `${packageId}::${CONTRACT_MODULE}::QuestionCreated`,
                },
                limit: 50,
                order: "descending",
            })

            const questionsList: Question[] = []

            for (const event of response.data) {
                const eventData = event.parsedJson as any
                const questionId = eventData.question_id

                try {
                    // Fetch chi ti·∫øt c√¢u h·ªèi
                    const questionObj = await iotaClient.getObject({
                        id: questionId,
                        options: { showContent: true },
                    })

                    if (questionObj.data?.content?.dataType === "moveObject") {
                        const fields = questionObj.data.content.fields as any
                        questionsList.push({
                            id: questionId,
                            questionText: bytesToString(fields.question_text || []),
                            hint: bytesToString(fields.hint || []),
                            rewardAmount: parseInt(fields.reward?.fields?.balance || "0"),
                            isActive: fields.is_active,
                            creator: fields.creator,
                            createdAt: parseInt(fields.created_at || "0"),
                            deadline: parseInt(fields.deadline || "0"),
                            winner: fields.winner?.fields?.value || null,
                        })
                    }
                } catch (err) {
                    console.error(`Error fetching question ${questionId}:`, err)
                }
            }

            setQuestions(questionsList)
        } catch (err) {
            console.error("Error fetching questions:", err)
        } finally {
            setIsLoading(false)
        }
    }, [packageId, iotaClient])

    /**
     * T·∫°o c√¢u h·ªèi m·ªõi
     */
    const createQuestion = async (
        questionText: string,
        hint: string,
        answer: string,
        salt: string,
        rewardAmount: number,
        deadline: number = 0
    ) => {
        if (!packageId || !gameStateId) {
            setError(new Error("Package ID ho·∫∑c Game State ID ch∆∞a ƒë∆∞·ª£c c·∫•u h√¨nh"))
            return
        }

        try {
            setIsLoading(true)
            setError(null)
            setHash(undefined)
            setIsConfirmed(false)

            // Debug log
            console.log("Creating question with:", {
                packageId,
                gameStateId,
                questionText,
                hint,
                answer,
                salt,
                rewardAmount,
                deadline
            })

            // Ki·ªÉm tra rewardAmount
            if (rewardAmount <= 0) {
                setError(new Error("Ti·ªÅn th∆∞·ªüng ph·∫£i l·ªõn h∆°n 0"))
                setIsLoading(false)
                return
            }

            // Hash ƒë√°p √°n
            const answerHash = hashAnswer(answer, salt)
            const answerHashBytes = hexToBytes(answerHash)

            console.log("Answer hash:", answerHash)
            console.log("Answer hash bytes:", answerHashBytes)

            const tx = new Transaction()

            // Set gas budget
            tx.setGasBudget(50000000)

            // Split coin cho reward
            const [rewardCoin] = tx.splitCoins(tx.gas, [tx.pure.u64(rewardAmount)])

            tx.moveCall({
                target: `${packageId}::${CONTRACT_MODULE}::${CONTRACT_METHODS.CREATE_QUESTION}`,
                arguments: [
                    tx.object(gameStateId),
                    tx.pure.vector("u8", stringToBytes(questionText)),
                    tx.pure.vector("u8", stringToBytes(hint)),
                    tx.pure.vector("u8", answerHashBytes),
                    rewardCoin,
                    tx.pure.u64(deadline),
                ],
            })

            signAndExecute(
                { transaction: tx as any },
                {
                    onSuccess: async ({ digest }) => {
                        setHash(digest)
                        await iotaClient.waitForTransaction({ digest })
                        setIsConfirmed(true)
                        await fetchQuestions()
                        await fetchGameStats()
                        setIsLoading(false)
                    },
                    onError: (err) => {
                        const error = err instanceof Error ? err : new Error(String(err))
                        setError(error)
                        console.error("Error creating question:", err)
                        setIsLoading(false)
                    },
                }
            )
        } catch (err) {
            const error = err instanceof Error ? err : new Error(String(err))
            setError(error)
            console.error("Error creating question:", err)
            setIsLoading(false)
        }
    }

    /**
     * Tr·∫£ l·ªùi c√¢u h·ªèi
     */
    const submitAnswer = async (
        questionId: string,
        answer: string,
        salt: string
    ) => {
        if (!packageId || !gameStateId) {
            setError(new Error("Package ID ho·∫∑c Game State ID ch∆∞a ƒë∆∞·ª£c c·∫•u h√¨nh"))
            return
        }

        try {
            setIsLoading(true)
            setError(null)
            setHash(undefined)
            setIsConfirmed(false)

            const tx = new Transaction()

            tx.moveCall({
                target: `${packageId}::${CONTRACT_MODULE}::${CONTRACT_METHODS.SUBMIT_ANSWER}`,
                arguments: [
                    tx.object(gameStateId),
                    tx.object(questionId),
                    tx.pure.vector("u8", stringToBytes(answer)),
                    tx.pure.vector("u8", stringToBytes(salt)),
                ],
            })

            signAndExecute(
                { transaction: tx as any },
                {
                    onSuccess: async ({ digest }) => {
                        setHash(digest)
                        await iotaClient.waitForTransaction({ digest })
                        setIsConfirmed(true)
                        await fetchQuestions()
                        await fetchGameStats()
                        setIsLoading(false)
                    },
                    onError: (err) => {
                        const error = err instanceof Error ? err : new Error(String(err))
                        setError(error)
                        console.error("Error submitting answer:", err)
                        setIsLoading(false)
                    },
                }
            )
        } catch (err) {
            const error = err instanceof Error ? err : new Error(String(err))
            setError(error)
            console.error("Error submitting answer:", err)
            setIsLoading(false)
        }
    }

    /**
     * H·ªßy c√¢u h·ªèi (ch·ªâ creator ho·∫∑c admin)
     */
    const cancelQuestion = async (questionId: string) => {
        if (!packageId || !gameStateId) {
            setError(new Error("Package ID ho·∫∑c Game State ID ch∆∞a ƒë∆∞·ª£c c·∫•u h√¨nh"))
            return
        }

        try {
            setIsLoading(true)
            setError(null)
            setHash(undefined)
            setIsConfirmed(false)

            const tx = new Transaction()

            tx.moveCall({
                target: `${packageId}::${CONTRACT_MODULE}::${CONTRACT_METHODS.CANCEL_QUESTION}`,
                arguments: [
                    tx.object(gameStateId),
                    tx.object(questionId),
                ],
            })

            signAndExecute(
                { transaction: tx as any },
                {
                    onSuccess: async ({ digest }) => {
                        setHash(digest)
                        await iotaClient.waitForTransaction({ digest })
                        setIsConfirmed(true)
                        await fetchQuestions()
                        await fetchGameStats()
                        setIsLoading(false)
                    },
                    onError: (err) => {
                        const error = err instanceof Error ? err : new Error(String(err))
                        setError(error)
                        console.error("Error cancelling question:", err)
                        setIsLoading(false)
                    },
                }
            )
        } catch (err) {
            const error = err instanceof Error ? err : new Error(String(err))
            setError(error)
            console.error("Error cancelling question:", err)
            setIsLoading(false)
        }
    }

    /**
     * Th√™m ti·ªÅn th∆∞·ªüng cho c√¢u h·ªèi
     */
    const addReward = async (questionId: string, amount: number) => {
        if (!packageId) {
            setError(new Error("Package ID ch∆∞a ƒë∆∞·ª£c c·∫•u h√¨nh"))
            return
        }

        try {
            setIsLoading(true)
            setError(null)
            setHash(undefined)
            setIsConfirmed(false)

            const tx = new Transaction()

            const [rewardCoin] = tx.splitCoins(tx.gas, [tx.pure.u64(amount)])

            tx.moveCall({
                target: `${packageId}::${CONTRACT_MODULE}::${CONTRACT_METHODS.ADD_REWARD}`,
                arguments: [
                    tx.object(questionId),
                    rewardCoin,
                ],
            })

            signAndExecute(
                { transaction: tx as any },
                {
                    onSuccess: async ({ digest }) => {
                        setHash(digest)
                        await iotaClient.waitForTransaction({ digest })
                        setIsConfirmed(true)
                        await fetchQuestions()
                        setIsLoading(false)
                    },
                    onError: (err) => {
                        const error = err instanceof Error ? err : new Error(String(err))
                        setError(error)
                        console.error("Error adding reward:", err)
                        setIsLoading(false)
                    },
                }
            )
        } catch (err) {
            const error = err instanceof Error ? err : new Error(String(err))
            setError(error)
            console.error("Error adding reward:", err)
            setIsLoading(false)
        }
    }

    // Auto-fetch khi c√≥ gameStateId
    useEffect(() => {
        if (gameStateId && packageId) {
            fetchQuestions()
            fetchGameStats()
        }
    }, [gameStateId, packageId, fetchQuestions, fetchGameStats])

    const transactionState: TransactionState = {
        isLoading: isLoading || isPending,
        isPending,
        hash,
        error,
        isConfirmed,
    }

    return {
        // State
        address,
        packageId,
        gameStateId,
        questions,
        gameStats,
        transactionState,

        // Actions
        createQuestion,
        submitAnswer,
        cancelQuestion,
        addReward,
        fetchQuestions,
        fetchGameStats,
        saveGameStateId,

        // Helpers
        hashAnswer,
        stringToBytes,
        hexToBytes,
    }
}
